.home-about {
  color: color(base);
  background-color: color(secondary);
  h2 {
    font-family: $primary;
    font-weight: weight(bold);
    font-size: type-scale(0);
    text-transform: uppercase;
  }
  svg {
    fill: color(base);
  }
  p {
    @extend %home-section-paragraphs;
  }
  .building-address {
    width: 100%;
  }
  input {
    display: block;
    margin: 0 auto;
    max-width: $base-line-height * 24;
    width: 100%;
    padding: $base-line-height / 2;
    font-size: type-scale(0);
    color: color(primary);
    text-align: center;
    background-color: color(base, base);
    border: none;
    border-radius: $base-line-height / 4;
  }
  .button {
    @include button-links;
    padding: type-scale(-1) $base-line-height;
    font-size: type-scale(0);
    font-family: $primary;
    line-height: line-height(0);
    text-transform: uppercase;
  }
}
