.home-about {
  @extend %home-section-padding;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  @media screen and (min-width: break(first)) {
    flex-grow: 1;
  }
  svg {
    order: 1;
    width: 48px;
    height: 48px;
  }
  h2 {
    order: 2;
    padding-top: $base-line-height;
    margin-bottom: type-scale(1);
  }
  p {
    order: 3;
    margin-bottom: $base-line-height;
  }
  .building-address {
    order: 4;

    input {
      display: inline-block;
      margin: 0 0 $base-line-height;
    }

    div {
      text-align: center;
    }

    .justify-inputs {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    @media screen and (min-width: break(first)) {
      max-width: 30rem;

      .justify-inputs {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
      }

      input {
        width: 30%;

        &[name="address"] {
          width: 100%;
        }
      }
    }
  }
  .button {
    order: 5;
  }
}
